<template>
  <div>
    <console-header></console-header>
    <hr/>
    <div>
      redis命令:<input type="text" v-model="redisCommand.name" placeholder="请输入要执行的命令"/>
      <br/>
      <button v-on:click="execute">执行</button>
      <br/>
      执行结果：<textarea cols="30" rows="10" v-model="responseResult"></textarea>
    </div>
    <hr/>
    <console-footer></console-footer>
  </div>
</template>

<script>
  import ConsoleHeader from '../common/ConsoleHeader'
  import ConsoleFooter from '../common/ConsoleFooter'

  export default {
    name: 'Index',
    // consoleHeader、consoleFooter组件给申明到components里面然后在template里面使用
    components: {ConsoleFooter, ConsoleHeader},
    data () {
      return {
        redisCommand: {name: ''},
        responseResult: []
      }
    },
    methods: {
      execute () {
        this.$axios
          .post('/api/execute', {
            command: this.redisCommand.name,
          })
          .then(successResponse => {
            this.responseResult = JSON.stringify(successResponse.data)
            if (successResponse.data.code === 200) {
              this.$router.replace({path: '/'})
            }
          })
          .catch(failResponse => {
            console.log(failResponse.message)
          })
      }
    }
  }
</script>



