<template>
  <div>
    <console-header></console-header>
    <hr/>
    <div>
      用户名:<input type="text" v-model="loginInfoVo.username" placeholder="请输入用户名"/>
      <br/>
      密码: <input type="password" v-model="loginInfoVo.password" placeholder="请输入密码"/>
      <br/>
      <button v-on:click="login">登录</button>
      <br/>
      登录验证情况: <textarea cols="30" rows="10" v-model="responseResult"></textarea>
    </div>
    <hr/>
    <consoleFooter></consoleFooter>
  </div>
</template>

<script>
  import consoleHeader from '@/components/common/ConsoleHeader.vue'
  import consoleFooter from '@/components/common/ConsoleFooter.vue'
  import * as types from '../../store/types'

  export default {

    name: 'Login',
    // Header, Footer组件给声明到components里然后再template里面使用
    components: {consoleHeader, consoleFooter},
    data () {
      return {
        loginInfoVo: {username: '', password: ''},
        responseResult: []
      }
    },
    mounted () {
      this.$store.commit(types.TITLE, 'Login')
    },
    methods: {
      login () {
        this.$axios.post('/api/login', {
          username: this.loginInfoVo.username,
          password: this.loginInfoVo.password
        })
          .then(response => {
            this.responseResult = JSON.stringify(response.data)
            if (response.data.code === 200) {
              this.token = response.data.data
              this.$store.commit(types.LOGIN, this.token)
              let redirect = decodeURIComponent(this.$route.query.redirect || '/')
              this.$router.push({
                path: redirect
              })
            }
          })
          .catch(err => {
            console.log('err', err)
          })
      }
    }
  }
</script>

<style scoped>

</style>



